<template>
    <div class="page-content">
        <!-- 추후 form component file로 분할-->
        <div class="form-wrapper">
            <form action="" @submit.prevent="onSubmitQuery">
                <div class="d-grid lg:grid-cols-4 md:grid-cols-3 grid-cols-1 gap-3">
                    <div class="form-group">
                        <flat-pickr v-model="fromDate" id="date_from" :config="config" placeholder="기간 설정(7일 이내)" class="form-control"></flat-pickr>
                    </div>
                    <div class="form-group">
                        <flat-pickr v-model="toDate" id="date_to" :config="config2" placeholder="기간 설정(7일 이내)" class="form-control"></flat-pickr>
                    </div>
                    <div class="btn-group sm:grid-colspan-2">
                            <div @click="setPeriod(0,0,0)" class="button">당일</div>
                            <div @click="setPeriod(0,0,2)" class="button">3일</div>
                            <div @click="setPeriod(0,0,6)" class="button">일주일</div>
                            <div @click="setPeriod(0,1,-1)" class="button">1개월</div>
                            <div @click="setPeriod(0,3,-1)" class="button">3개월</div>
                    </div>
                    <!-- <div class="sm:grid-colspan-2 d-flex gap-3">
                        <div class="btn-group">
                            <div @click="onFormattedQuery('1d')" class="button">당일</div>
                            <div @click="onFormattedQuery('3d')" class="button">3일</div>
                            <div @click="onFormattedQuery('1w')" class="button">일주일</div>
                            <div @click="onFormattedQuery('1m')" class="button">1개월</div>
                            <div @click="onFormattedQuery('3m')" class="button">3개월</div>
                        </div>
                    </div> -->
                    <div v-if="smode=='pay'" class="lg:grid-colspan-4  md:grid-colspan-3 sm:grid-colspan-2 d-grid lg:grid-cols-4 sm:grid-cols-2 gap-3">
                        <div class="form-group">
                            <input type="text" v-model="kw.uname" name="username" placeholder="주문자 이름"/>
                        </div>
                        <div class="form-group">
                            <input type="text" v-model="kw.publisher" name="publisher" placeholder="출판사 이름"/>
                        </div>
                        <div class="form-group">
                            <input type="text" v-model="kw.isbn13" name="isbn13" placeholder="ISBN"/>
                        </div>
                        <div class="form-group">
                            <input type="text" v-model="kw.qtid" name="publisher" placeholder="주문번호"/>
                        </div>
                    </div>
                </div>
                <div class="d-grid lg:grid-cols-4 md:grid-cols-3 gap-3 mt-3r">
                    <div class="nav-group fluid">
                        <label>
                            <input type="radio" name="smode" v-model="smode" value="order" checked/> 
                            <span class="button">발주</span>
                        </label>
                        <label>
                            <input type="radio" name="smode" v-model="smode" value="pay"/> 
                            <span class="button">결제</span>
                        </label>
                    </div>
                    <div class="form-group">
                        <button type="submit" class="btn-block">조회</button>
                    </div>
                </div>
            </form>
        </div>
        <!-- <result-tras :results='res' @openDet='openDetail'></result-tras> -->
        <!-- <div id="loader_1"></div> -->
        <div id='loader_1' v-show="loading"></div>
        <result-btras v-show="showResults" @authorized="setAuthorized"></result-btras>
    </div>
</template>
<script>
import api from '@/utils/api'
import { mapGetters } from 'vuex'
import ResultBtras from './ResultBtras.vue'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import { Korean } from "flatpickr/dist/l10n/ko.js"

export default {
    name:'SearchBtras',
    components: {
        ResultBtras,
        flatPickr
    },
    data() {
        return {
            fromDate:new Date(),
            toDate:new Date(),
            smode:"order",
            kw: {
                uname:"",
                isbn13:"",
                qtid:"",
                publisher:"",
                email:"",
            },
            config:{ 
                locale: Korean, 
                maxDate:"today", 
                minDate:null,
                dateFormat:"Y-m-d",
            },
            config2:{
                locale: Korean, 
                maxDate:"today", 
                dateFormat:"Y-m-d",
                enable:["today"],
            },
            loading:false,
        }
    },
    methods:{
        //lazyLoadJson:()=>import('@/assets/json/transaction_modified.json'),

        // 달력 UI 시작시점 설정하기: 최종일 포함 7일전까지 선택 가능
        setMinDate: function(toDate) {
            const max = new Date(toDate)
            let min = new Date()
            min.setFullYear(max.getFullYear())
            min.setMonth(max.getMonth())
            min.setDate(max.getDate() - 6)
            this.config.minDate = min
            //return min
        },
        
        // 달력 UI에서 선택한 기간 구하기
        getPeriod() {
            // get period
            const miliDif = (new Date(this.toDate)).getTime() - (new Date(this.fromDate)).getTime()
            console.log(miliDif, Math.abs(miliDif / (1000*60*60*24)))
            return (Math.abs(miliDif / (1000*60*60*24))+1)
        },

        maskName: name=>{
            if (name.length <= 2) {
                return name.replace(name.substring(0, 1), "*");
            }

            return (
                name[0] +
                "*".repeat(name.substring(1, name.length - 1).length) +
                name[name.length - 1]
            );
        },
        // submit button(Search)
        onSubmitQuery: async function() {
            // 이전에 조회한 데이터를 유지하지 않음
            this.$store.commit('setResults', false)
            this.loading = true

            try {
                //window.alert(this.getPeriod())
                let res = await api.btras(`${this.getPeriod()}d`,this.toDate,this.smode,this.kw)
                
                console.log(res.data)
                if(res.data.result!="denial") {
                    res.data.tras.forEach(tra=>{
                        tra.name_m = this.maskName(tra.urname);
                    });
                }
                this.$store.dispatch('loadTransactions', res.data);
                this.$store.commit('setResults', true)

                this.loading = false
            } catch(error) {
                this.loading = false
                console.log(error.message)
                //document.getElementById('loader_1').style.display = 'none';
            }

        },
        // 우측 단축키로 달력 UI 시작지점 설정하기
        setPeriod: function(offsetY, offsetM, offsetD) {
            var enday = new Date(this.toDate)
            enday.setFullYear(enday.getFullYear() - offsetY)
            enday.setMonth(enday.getMonth() - offsetM)
            enday.setDate(enday.getDate() - offsetD)
            this.fromDate = enday
        },
        onFormattedQuery: async function(period){
            // api.btras
            // show loader in loading data
            //this.showResults = false
            this.$store.commit('setResults', false)
            this.loading = true

            try {
                // let res = await api.btras(this.getPeriod())
                // criterion: 'po' 출판사 발주 기준, 'up' 사용자 결제 기준 
                let res = await api.btras(period, this.toDate, 'po')
                
                console.log(res.data)
                if(res.data.result!="denial") {
                // if(res.data.length>0) {
                    res.data.tras.forEach(tra=>{
                        tra.name_m = this.maskName(tra.urname);
                    });
                }
                this.$store.dispatch('loadTransactions', res.data);

                //this.showResults = true
                this.$store.commit('setResults', true)

                this.loading = false
            } catch(error) {
                this.loading = false
                console.log(error.message)
                //document.getElementById('loader_1').style.display = 'none';
            }
        },
        /*openDetail(item) {
            this.$emit('openDet',item);
        },*/
        setAuthorized(status) {
            this.$emit('authorized',status)
        },
    },
    mounted(){ 
        // 내역조회 UI가 열릴 때는 뷰모드를 search로
        this.$emit('viewMode','search')
         // 메뉴가 열릴 때 초기 조회기간 UI 설정
        // 상세보기에서 복귀할 때는 부모 콤포넌트에서 가져와 복원
        if(!this.showResults) {
            const now = new Date()
            this.toDate = now.getFullYear().toString() + '-' + ('0'+(now.getMonth()+1)).slice(-2) + '-' + ('0'+(now.getDate())).slice(-2)
            this.fromDate = this.toDate
        }
        else {
            this.toDate = this.$parent.toDate
            this.fromDate = this.$parent.fromDate
        }
        // 캘린더 조회 가능 시작일(7일 전) 세팅 
        //this.config.minDate = this.setMinDate()
        this.setMinDate(this.toDate)
    },
    computed: {
        ...mapGetters({
            countsOfTras:'getCounts',
            results:'tras',
            // 검색결과 테이블을 보여줄 지 여부
            showResults:'keepResults'  
        }),
        reAuthorized() {
            return this.$parent.reAuthorized
        },
    },
    watch: {
        fromDate(newVal) {
            // parent component에 저장
            this.$emit('setPeriod',{ from:newVal, to:this.toDate });
        },
        toDate(newVal) {
            // parent component에 저장
            this.$emit('setPeriod',{ from:this.fromDate, to:newVal });
            // 최종일이 변경되면 시작일 범위와 가능일을 변경
            this.fromDate = newVal
            this.config.maxDate = this.fromDate
            this.setMinDate(newVal)
        }
    }
}
</script>
<style scoped>
.form-wrapper { max-width:800px; }
.form-group label { margin-bottom:5px }
.form-group input { border:0; }

.btn-group .button { border-color:var(--border-lightgrey); color:var(--secondary) }

.nav-group { border-color:transparent; }
.nav-group .button { color:var(--text-lightgrey);background-color:var(--bg-sunken); }
.nav-group input[type="radio"]:checked + .button {	background-color:var(--bg-white); color:var(--secondary); }

#loader_1 { margin:100px 0; position:relative; left:50%; top:50%; transform:translateX(-50%); border:6px solid var(--bg-white); border-top-color:var(--primary); border-radius:50%; width:60px; height:60px; -webkit-animation:spin 2s linear infinite; animation:spin 2s linear infinite; }
/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); } 
}
</style>